.switch {
	position: relative;
	display: inline-block;
	width: 40px;
	height: 25px;
	margin-bottom: 0px;
	margin-left: 10px;
	margin-right: 10px;
	z-index: 0;
  }
  
  .switch input { 
	opacity: 0;
	width: 0;
	height: 0;
  }
  
  .slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #2196F3;
	transition: .4s;
  }
  
  .slider:before {
	position: absolute;
	content: "";
	height: 18px;
	width: 18px;
	left: 4px;
	bottom: 4px;
	background-color: #ccc;
	transition: .4s;
  }
  
  input:checked + .slider {
	background-color: #2196F3;
  }
  
  input:focus + .slider {
	box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
	-webkit-transform: translateX(15px);
	transform: translateX(15px);
  }

  hr {
	  margin-top: 5px
  }

  #switch-div {
	  display: -webkit-flex;
	  display: flex;
	  -webkit-flex-direction: row;
	          flex-direction: row;
	  -webkit-justify-content: center;
	          justify-content: center;
	  -webkit-align-items: center;
	          align-items: center;
	  text-align: center;
  }
